
const isExcluded = (digit:number, excludes: string) => (excludes ? excludes.indexOf(digit + "") !== -1 : false);

const getCombos = (startDigit:number, digits:number, sum:number, excludes: string) : number[][] => {
    let result: number[][] = [];

    for (let digit: number = startDigit; digit > 1; digit--) {
        const remainder: number = sum - digit;

        if (remainder > 0 && !isExcluded(digit, excludes)) {
            if (digits === 2 && remainder < digit && !isExcluded(remainder, excludes)) {
                result.push([digit, remainder]);
            } else if (digits > 2) {
                const moreCombos = getCombos(digit - 1, digits - 1, remainder, excludes);

                if (moreCombos.length !== 0) {
                    const addDigit = moreCombos.map((combo) => [digit, ...combo]);

                    result = [...result, ...addDigit];
                }
            }
        }
    }

    return result;
};


const comboStrings = (digits: string, sum: string, excludes: string) : string[] => {
    const combos = getCombos(9, parseInt(digits), parseInt(sum), excludes);

    return combos.map((combo) => combo.join(""));
}

export default comboStrings;
