import styled from "styled-components";
import { TextInput, Button } from "@carbon/react";
import { useState } from "react";
import findCombos from "../combos";

const MainContainer = styled.div`
  margin: 15px;
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 50%;
`;

const StyledResults = styled.div`
  margin-top: 20px;
`;

const StyledButton = styled(Button)`
  margin-top: 10px;
`;

const Results = (props: { results: string[] }) => {
  return (
    <StyledResults>
      Results:
      {props.results.map((result) => (
        <>
          <br/>
          {result}
        </>
      ))}
    </StyledResults>
  );
};

const FindCombosMain = () => {
  const [digits, setDigits] = useState("");
  const [sum, setSum] = useState("");
  const [excludes, setExcludes] = useState("");
  const [results, setResults] = useState<string[]>([]);

  const handleCombos = () => {
    setResults(findCombos(digits, sum, excludes));
  };

  return (
    <MainContainer>
      <TextInput
        id="digits"
        labelText="Digits:"
        inline={true}
        value={digits}
        onChange={(event) => setDigits(event.target.value)}
      />
      <TextInput
        id="sum"
        labelText="Sum:"
        inline={true}
        value={sum}
        onChange={(event) => setSum(event.target.value)}
      />
      <TextInput
        id="excludes"
        labelText="Excludes:"
        inline={true}
        value={excludes}
        onChange={(event) => setExcludes(event.target.value)}
      />
      <StyledButton onClick={() => handleCombos()}>Find Combos</StyledButton>
      <Results results={results} />
    </MainContainer>
  );
};

export default FindCombosMain;
